import * as React from 'react';
import * as style from './style.css';
import { Route, BrowserRouter, Switch } from 'react-router-dom';
import { history } from 'app/utils';
import { Header, Footer, OverlayNav } from 'app/components';
import { 
	HomePage, 
	PhotoPage, 
	AboutPage, 
	ContactPage, 
	WorkPage, 
	WorkDescriptionPage,
	NotFoundPage } from './pages';
import update from 'immutability-helper';
import { Ref } from 'semantic-ui-react';

export namespace App {
	export interface Props {}

	export interface State {
		overlayNav: boolean;
	}
}

export class App extends React.Component<App.Props, App.State> {
	public contextRef: React.RefObject<JSX.Element> | null = null;
	public defaultTitle: string = 'Kristoffer Robin Canlas';

	constructor(props: App.Props, context?: any) {
		super(props, context);

		this.state = {
			overlayNav: false
		};

		this.contextRef = React.createRef();
		this.toggleOverlay = this.toggleOverlay.bind(this);
		this.changeTitle = this.changeTitle.bind(this);
	}

	public componentDidMount() {
		this.changeTitle();
		history.listen(() => {
			this.changeTitle();
		});
	}

	public changeTitle() {
		const currentPath: string = history.location.pathname.split('/')[1].toUpperCase();
		const title: string = currentPath === '' ? this.defaultTitle : `${this.defaultTitle} | ${currentPath}`;
		document.title = title;
	}

	public toggleOverlay() {
		this.setState(update(this.state, {
			overlayNav: { $set: !this.state.overlayNav } 
		}));
	}

	public render() {
		return (
			<BrowserRouter>
				<span className={style.bg}></span>
				<OverlayNav overlayNav={this.state.overlayNav} toggleOverlay={this.toggleOverlay} />
				<Ref innerRef={this.contextRef}>
					<span className={style.content}>
						<Header contextRef={this.contextRef} toggleOverLay={this.toggleOverlay} />
						<span className={style.body}>
							<Switch>
								<Route exact path='/' component={HomePage} />
								<Route path='/about' component={AboutPage} />
								<Route exact path='/work' component={WorkPage} />
								<Route path='/projects/:id' component={WorkDescriptionPage} />
								<Route path='/photography' component={PhotoPage} />
								<Route path='/contact' component={ContactPage} />
								<Route path='*' component={NotFoundPage} />
							</Switch>
						</span>
						<Footer />
					</span>
				</Ref>
			</BrowserRouter>
		);
	}
}
